import React from "react";
import styled from "styled-components";
import { Link, useStaticQuery, graphql } from "gatsby";

const Contaner = styled.div`
  position: sticky;
  display: flex;
  flex-direction: column;
  padding: 1rem 2rem;
  left: 0;
`;

export const TagList = () => {
  const data = useStaticQuery(graphql`
    {
      allMarkdownRemark {
        totalCount
        group(field: frontmatter___tags) {
          tag: fieldValue
          totalCount
        }
      }
    }
  `);
  const tagList = data.allMarkdownRemark.group || null;
  return (
    <Contaner>
      <span></span>
      <Link key={0} to={`/blog`}>
        {`(${data.allMarkdownRemark.totalCount})`}
      </Link>
      {tagList
        ? tagList.map((e, idx) => (
            <Link
              key={idx + 1}
              to={`/?tagName=${e.tag}`}
            >{`${e.tag}(${e.totalCount})`}</Link>
          ))
        : null}
    </Contaner>
  );
};
